h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-main);
  background-color: var(--section-white);
  letter-spacing: .04em;
  font-family: DM sans, sans-serif;
  font-size: 16px;
  line-height: 1.76;
}

.list {
  padding-left: 0;
  list-style-type: none;
}

:root {
  --text-main: #fff;
  --text-accent: #d41443;
  --hero-bg: #ffb8ca;
  --section-white: #e5e5e5;
  --section-rose: #fff5f6;
  --paragraf-black: #000;
  --paragraf-grey: #907e82;
  --border-in: #e1e1e1;
  --ico-bg: #fea5bb;
  --href-top: #ffa5ba;
  --bg-btn: #d41342;
  --bg-btn-hover-red: #c7133e;
  --bg-btn-hover-white: #ecebeb;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  outline: 2px solid tomato;
