.backdrop-about.is-hidden .about-modal {
  transform: translate(-50%, -50%)scale(1.1);
}

.about-modal {
  height: auto;
  width: 300px;
  z-index: 105;
  background-color: #e17992;
  padding: 30px 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 768px) {
  .about-modal {
    width: 700px;
    padding: 115px 105px;
  }
}

.about-modal__close-button {
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

.about-modal__close-button:before, .about-modal__close-button:after {
  content: " ";
  height: 13.57px;
  width: 2px;
  background-color: #fff;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
}

.about-modal__close-button:before {
  transform: translate(-50%, -50%)rotate(45deg);
}

.about-modal__close-button:after {
  transform: translate(-50%, -50%)rotate(-45deg);
}

.about-modal__close-button:hover:before, .about-modal__close-button:hover:after {
  background-color: var(--text-accent);
}

